<template>
  <div class="icons">
    <swiper :options="swiperOption">
        <swiper-slide v-for="(page, index) of pages" :key="index">
          <router-link
            tag="div"
            class="icon"
            v-for="item of page"
            :key="item.id"
            :to="'/strategy/' + item.id"
          >
            <div class="icon-img">
              <img class="icon-img-content" :src="item.imgUrl">
              <p class="icon-desc">{{item.desc}}</p>
            </div>
          </router-link>
        </swiper-slide>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        autoplay: false
      }
    }
  },
  computed: {
    pages () {
      const pages = []
      this.list.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'
  .icons >>> .swiper-container{
    height: 0;
    padding-bottom: 50%;
  }
  .icons{
    margin-top: .2rem;
  }
    .icon{
      position: relative;
      overflow: hidden;
      float: left;
      width: 25%;
      height: 0;
      padding-bottom: 25%;
    }
      .icon-img{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: .44rem;
        box-sizing: border-box;
        padding: .3rem;
      }
        .icon-img-content{
          display: block;
          overflow: hidden;
          margin: 0 auto;
          height: 100%;
        }
      .icon-desc{
        position: absolute;
        left: 0;
        right: 0;
        height: .70rem;
        line-height: .70rem;
        text-align: center;
        color: $darkTextColor;
        ellipsis();
      }
</style>



